.ButtonLink{
    
    background: #000d1a;
    white-space: nowrap;
    outline: none;
    border: none;
    min-width: 100px;
    max-width: 200px;
    cursor: pointer;
    text-decoration: none;
    transition: 0.3s;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 15px 30px;
    color: #fff;
    font-size: 14px;
}

.ButtonLink:hover{
    transform: translateY(-2px);
    color: #000d1a;
    background: #ffc500;
    transition: 0.2s ease-out;
    cursor: pointer;
}

.ButtonLink2{
    
    background: #ffc500;
    max-width: 160px;
    padding: 10px 12px;
    color:#000d1a;
    font-size: 10px;
}

.ButtonLink2:hover{
    transform: translateY(-2px);
    color: #fff;
    background: #000d1a;
    transition: 0.2s ease-out;
    cursor: pointer;
}

.ButtonLink3{

font-size: 1.4rem;
