.Link{
    color:#fff;
    display: flex;
    align-items: center;
    padding: 0 1rem;
    height: 100%;
    cursor: pointer;
    text-decoration: none;
    font-size: 14px;
}

.LinkMenu{
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-size: 1.5rem;
    text-decoration: none;
    list-style: none;
    color:#fff;
    cursor: pointer;
    transition: 0.2s ease-in-out;
}

.LinkMenu:hover{
    color: #000d1a;
}